import { mapValuesToThis } from '@jscrpt/common';
/**
 * Rest relations options model
 */

export class RestRelationsOptionsModel {
  //######################### constructor #########################
  constructor(value) {
    //######################### public properties #########################

    /**
     * @inheritdoc
     */
    this.url = '';
    /**
     * @inheritdoc
     */

    this.method = 'GET';
    /**
     * @inheritdoc
     */

    this.runImmediately = true;
    mapValuesToThis.bind(this)(value);
  }

}