import { mapValuesToThis } from '@jscrpt/common';
/**
 * Rest param model
 */

export class RestParamModel {
  //######################### constructor #########################
  constructor(value) {
    //######################### public properties #########################

    /**
     * @inheritdoc
     */
    this.type = 'PATH';
    /**
     * @inheritdoc
     */

    this.configurable = false;
    /**
     * @inheritdoc
     */

    this.name = null;
    mapValuesToThis.bind(this)(value);
  }

}